@keyframes myAnim {
  0% {
    background-size: 20%;
  }

  100% {
    background-size: 8%;
  }
}
.showWorkData {
  width: 100vw;
  height: 100%;
  position: fixed;
  top: 0;
  transition: all ease 0.2s;
  padding: 0 50px;
  padding: 0 0px 0 40vw;
  right: 0;
  z-index: 11;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: 60px calc(100% - 60px);
  animation: myAnim 0.4s ease 0s 1 normal forwards;
}
.showWorkOff .showWorkData {
  background-color: transparent;
}
.showWorkOff .showWorkData .inner {
  @keyframes myAnim2 {
    0% {
      transform: translateX(0%);
    }

    100% {
      transform: translateX(110%);
    }
  }
  animation: myAnim2 0.2s ease-out 0s 1 normal forwards;
}
@keyframes myAnim1 {
  0% {
    transform: translateX(110%);
  }

  100% {
    transform: translateX(0%);
  }
}
.showWorkData .inner {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: #393939;
  transition: all ease 0.5s;
  border-left: 1px solid #dcdcdc;
  padding-left: 100px;
  padding-right: 100px;
  animation: myAnim1 0.4s ease 0s 1 normal forwards;
  color: #fff;
}
.showWorkData .inner a {
  color: #fff;
}
h2.subTitle {
  margin: 35px 0 20px;
  display: flex;
  justify-content: space-between;
}
span.iconClose {
  cursor: pointer;
}
.breadCrumbs {
  margin: 0 0 10px;
  font-size: 0.8rem;
}

.showWorkData img {
  max-width: 100%;
  width: 100%;
}
.usedSkills {
  margin: 0px 0 50px;
  border: 1px solid rgb(0 0 0 / 0.06);
  padding: 30px;
  background: rgba(204, 204, 204, 0.07);
}
.usedSkills li {
  padding: 0 0 10px;
}
h2.headPara {
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;
  margin-bottom: 50px;
}
.workPicPanel {
  border: 16px solid rgb(0 0 0 / 1);
}
.workDataTale {
  border-top: 1px solid #ccc;
  padding: 35px 0 60px;
  margin-top: 50px;
}
.workDataTale a {
  margin-right: 30px;
  background: rgb(33, 33, 33);
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  text-decoration: none;
  transition: all ease 0.4s;
  &:hover {
    background-color: #515151;
  }
}
.footer {
  z-index: 4;
}
