.aneesWork {
  padding: 70px 0;
  background-color: #000;
}
.container {
  max-width: 1200px;
  width: 100%;
  margin: auto;
}
ul.logoList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@keyframes myAnim1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes myAnim2 {
  0% {
    max-height: 0;
    max-width: 0;
    opacity: 0;
  }

  100% {
    max-height: 100px;
    max-width: 220px;
    opacity: 1;
  }
}

ul.logoList li {
  list-style: none;
  padding: 40px 46px;
  display: flex;
  align-items: center;
  border: 1px solid rgb(204 204 204 / 25%);
  margin: 14px;
  background-color: #fff;
  animation: myAnim1 0.4s ease 0s 1 normal forwards;
  opacity: 0;
  min-width: 315px;
  align-items: center;
  justify-content: center;
  &:hover {
    opacity: 1;
  }
}

ul.logoList .logoItem img {
  max-height: 100px;
  max-width: 220px;
  filter: grayscale(100%);
  transition: all ease 0.1s;
  animation: myAnim2 0.1s ease 0s 1 normal forwards;
  &:hover {
    filter: grayscale(0%);
  }
}

